import React, { useEffect, useRef } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// Import your icons
import angular from "@styles/assets/images/token-dev-company/tech-icons/angular.svg";
import codeigniter from "@styles/assets/images/token-dev-company/tech-icons/codeigniter.svg";
import couchdb from "@styles/assets/images/token-dev-company/tech-icons/couchdb.svg";
import golang from "@styles/assets/images/token-dev-company/tech-icons/golang.svg";
import laravel from "@styles/assets/images/token-dev-company/tech-icons/laravel.svg";
import mongo from "@styles/assets/images/token-dev-company/tech-icons/mongo.svg";
import mysql from "@styles/assets/images/token-dev-company/tech-icons/mysql.svg";
import node from "@styles/assets/images/token-dev-company/tech-icons/node.svg";
import php from "@styles/assets/images/token-dev-company/tech-icons/php.svg";
import postgresql from "@styles/assets/images/token-dev-company/tech-icons/postgresql.svg";
import react from "@styles/assets/images/token-dev-company/tech-icons/react.svg";

export const TokenDevelopmentTechnologyCard = ({ data }) => (
  <div className="token-dev-technology-card item-center">
    <div className="flex-center">
      <img src={data.icon} alt={data.title} />
      <p className="about-p">{data.title}</p>
    </div>
  </div>
);

const datas = [
  { id: 0, title: "Angular", icon: angular },
  { id: 1, title: "Codeigniter", icon: codeigniter },
  { id: 2, title: "CouchDB", icon: couchdb },
  { id: 3, title: "Golang", icon: golang },
  { id: 4, title: "Laravel", icon: laravel },
  { id: 5, title: "MongoDB", icon: mongo },
  { id: 6, title: "MySQL", icon: mysql },
  { id: 7, title: "Node", icon: node },
  { id: 8, title: "PHP", icon: php },
  { id: 9, title: "PostgreSQL", icon: postgresql },
  { id: 10, title: "React", icon: react },
];

const TokenDevelopmentTechnology = () => {
  const topMarqueeRef = useRef(null);
  const bottomMarqueeRef = useRef(null);
  const sectionRef = useRef(null);

  // Register ScrollTrigger plugin
  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
  }, []);

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    const createMarqueeTween = (container, direction = "left") => {
      const items = container.querySelectorAll(".token-dev-technology-card");
      const firstItem = items[0];

      if (!firstItem) return null;

      const itemWidth = firstItem.offsetWidth;
      const gap = parseInt(getComputedStyle(container).gap) || 0;
      const itemTotalWidth = itemWidth + gap;
      const totalItems = items.length;
      const totalWidth = itemTotalWidth * totalItems;

      // Set initial position
      gsap.set(container, { x: 0 });

      return gsap.to(container, {
        x: direction === "left" ? `-=${totalWidth / 2}` : `+=${totalWidth / 2}`,
        duration: totalItems * 2, // Adjust speed if needed
        ease: "none",
        repeat: -1,
        modifiers: {
          x: gsap.utils.unitize((x) => {
            const parsedX = parseFloat(x);
            return (
              (parsedX % (totalWidth / 2)) +
              (direction === "left" ? 0 : -totalWidth / 2)
            );
          }),
        },
      });
    };

    const topTween = createMarqueeTween(topMarqueeRef.current, "left");
    const bottomTween = createMarqueeTween(bottomMarqueeRef.current, "right");

    let lastScrollY = window.scrollY;

    const onScroll = () => {
      const currentScrollY = window.scrollY;
      const direction = currentScrollY > lastScrollY ? "down" : "up";

      if (direction === "down") {
        topTween?.timeScale(1);
        bottomTween?.timeScale(1);
      } else {
        topTween?.timeScale(-1);
        bottomTween?.timeScale(-1);
      }

      lastScrollY = currentScrollY;
    };

    window.addEventListener("scroll", onScroll);

    return () => {
      window.removeEventListener("scroll", onScroll);
      topTween?.kill();
      bottomTween?.kill();
    };
  }, []);

  return (
    <section className="token-dev-technology-main" ref={sectionRef}>
      <div className="token-dev-technology item-center">
        <div className="token-dev-technology-head item-center">
          <h3 className="about-h3">Our Technology Stack</h3>
        </div>
      </div>

      <section className="token-dev-technology-marquee-section">
        {/* Top Marquee (scrolls left) */}
        <div className="token-dev-technology-marquee" ref={topMarqueeRef}>
          {[...datas, ...datas].map((data, i) => (
            <TokenDevelopmentTechnologyCard key={`top-${i}`} data={data} />
          ))}
        </div>

        {/* Bottom Marquee (scrolls right) */}
        <div className="token-dev-technology-marquee" ref={bottomMarqueeRef}>
          {[...datas, ...datas].map((data, i) => (
            <TokenDevelopmentTechnologyCard key={`bottom-${i}`} data={data} />
          ))}
        </div>
      </section>
    </section>
  );
};

export default TokenDevelopmentTechnology;
