import React, { useEffect, useRef } from "react";
import { Link } from "@reach/router";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import TalkToUsNew from "@components/common/talk-to-us-new";
import processImage from "@styles/assets/images/token-dev-company/process-image.png";

// Register the plugin
gsap.registerPlugin(ScrollTrigger);

const TokenDevelopmentProcess = ({ db }) => {
  const containerRef = useRef(null);
  const imageRef = useRef(null);

  useEffect(() => {
    const screenWidth = window.innerWidth;

    // Example logic to vary movement:
    // More movement on desktop, less on mobile
    const moveInX = screenWidth > 768 ? "20%" : "50%";
    const moveOutX = screenWidth > 768 ? "-20%" : "-50%";

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: containerRef.current,
        start: "center center",
        end: "+=200%",
        scrub: true,
        pin: true,
      },
    });

    tl.fromTo(imageRef.current, { x: moveInX }, { x: "0%", ease: "none" });

    tl.to(imageRef.current, { x: moveOutX, ease: "none" });

    return () => {
      tl.scrollTrigger?.kill();
      tl.kill();
    };
  }, []);

  return (
    <section className="token-dev-main">
      <section className="token-dev-header">
        <div className="item-center">
          <h3 className="about-h3">Token Development Process</h3>
          <p className="about-p">
            Our token development is a well-organized method aimed at providing
            secure and high-performance tokens to suit your business needs. We
            help with every stage of the process, taken with precision and
            transparency between ideation and deployment.
          </p>
          <div className="buttons">
            <Link to="/contact-us" className="roll-button roll-btn">
              <span>View Live Demo</span>
            </Link>
            <TalkToUsNew
              db={db}
              label="Free Consultation"
              customClass="normal-roll-button normal-roll-btn"
            />
          </div>
        </div>
      </section>

      {/* Horizontal Scroll Section */}
      <section
        className="token-dev-horizontal-section horizontal-section"
        ref={containerRef}
      >
        <div className="box image item-center">
          <img ref={imageRef} src={processImage} alt="processImage" />
        </div>
      </section>
    </section>
  );
};

export default TokenDevelopmentProcess;
