import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import { useLocation } from "@reach/router";
import TokenDevelopment from "@screens/token-development-company";

const TokenDevelopmentCompany = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Token Development Company", link: null },
  ];
  const location = useLocation();
  const { data } = props;
  const fileUrl =
    data?.cryptoMlmImg?.childImageSharp?.gatsbyImageData?.images?.fallback?.src;
  const validData = data?.allMetaJson?.nodes[0];
  return (
    <Layout db={db}>
      <Seo
        title={validData?.title}
        metaTitle={validData?.metaTitle}
        image={fileUrl}
        description={validData?.description}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={location.pathname.split("/")?.[1]}
      ></Seo>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <TokenDevelopment db={db} />
      </main>
    </Layout>
  );
};

export default TokenDevelopmentCompany;
export const queryData = graphql`
  query MyQuery {
    cryptoMlmImg: file(relativePath: { eq: "token-development-company.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allMetaJson(filter: { slug: { eq: "token-development-company" } }) {
      nodes {
        metaTitle
        title
        slug
        description
      }
    }
  }
`;
